import React from 'react';
import { 
    Accordion,
    AccordionSummary,
    AccordionDetails,
    Typography,
    Box,
    makeStyles
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { icons } from '../../constants/typeIcons';

const useStyles = makeStyles((theme) => ({
    image: {
        width: '64px',
        marginRight: '0.5rem'
    },
    veritcalFlex: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center'
    },
    redBackground: {
        backgroundColor: `${theme.palette.primary.main} !important`
    }
}));

function PokemonInfo(props) {
    const classes = useStyles();
    const {type, name, number, children, expanded} = props;
    return (
        <Accordion TransitionProps={{ unmountOnExit: true }} expanded={expanded}>
            <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
            >
                <img 
                    src={`https://serebii.net/pokearth/sprites/crystal/${number}.png`}
                    alt={name}
                    className={classes.image} 
                />
                <Box className={classes.veritcalFlex}>
                    <Typography>{name}</Typography>
                    <Typography variant="body2">{type}</Typography>
                </Box>
            </AccordionSummary>
            <AccordionDetails>
                <Box>{children}</Box>
            </AccordionDetails>
        </Accordion>
    );
}

export default PokemonInfo;

